body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --color-primary: rgb(29, 161, 242);
  --color-black: rgb(20, 23, 26);
  --color-dark-gray: rgb(101, 119, 134);
  --color-light-gray: rgb(170, 184, 194);
  --color-extra-light-gray: rgb(225, 232, 237);
  --color-extra-extra-light-gray: rgb(245, 248, 250);
  --color-white: rgb(255, 255, 255);
}

* {
  box-sizing: border-box;
}

[class*="col-"] {
  width: 100%;
}

.col-3.title {
  width: 37.5%;
}

.col-5.title {
  width: 62.5%;
}

html, body, .main {
  margin: 0px;
  padding: 0px;
}

.main {min-height: 100vh;}

body {
  background-color: rgb(29, 161, 242);
  background-color: var(--color-primary);
  font-family: 'Helvetica Neue 55 Roman', Helvetica, Arial, "Lucida Grande", sans-serif; 
}

@font-face {
  font-family: 'Helvetica Neue 55 Roman';
  font-style: normal;
  font-weight: normal;
  src: local('Helvetica 55 Roman'), url(/static/media/HelveticaNeue-Roman.e6a530e1.woff) format('woff');
}

@font-face {
  font-family: 'Helvetica Neue 75 Bold';
  font-style: normal;
  font-weight: normal;
  src: local('Helvetica 75 Bold'), url(/static/media/HelveticaNeue-Bold.0c7d5d33.woff) format('woff');
}

[class*="col-"] {
  float: left;
  padding: 1.5vmin;
}

.row::after {
  content: "";
  clear: both;
  display: table;
}

h1 {
  padding: 2vw;
  margin: 0px;
  font-size: 7vw;
  text-align: right;
  font-family: 'Helvetica Neue 75 Bold', Helvetica;
}

h2 {
  padding-left: 2vw;
  margin: 0px;
  font-size: 4vw;
}

h3 {
  padding: 0vmin;
  font-size: 2.5vmin;
  color: rgb(20, 23, 26);
  color: var(--color-black);
  font-family: 'Helvetica Neue 75 Bold', Helvetica;
}

.page-title {
  vertical-align: bottom;
  padding-top: 2vmin;
}

.title-box {
  margin: 0.5vmin;
  width: calc(100% - 1vmin);
  background-color: rgb(255, 255, 255);
  background-color: var(--color-white);
}

.page-heading-box {
  background-color: rgb(225, 232, 237);
  background-color: var(--color-extra-light-gray);
  margin: 1.5vmin;
}

.border {
  border: 0.5vmin solid rgb(170, 184, 194);
  border: 0.5vmin solid var(--color-light-gray);
}

.round-border {
  border: 0.5vmin solid rgb(101, 119, 134);
  border: 0.5vmin solid var(--color-dark-gray);
  border-radius: 1vmin;
}

.center {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.right {
  text-align: right;
}

.bottom {
  vertical-align: text-bottom;
}

.top {
  vertical-align: text-top;
}

.sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.navBar {
  background-color: rgb(170, 184, 194);
  background-color: var(--color-light-gray);
  border: 0.5vmin solid rgb(101, 119, 134);
  border: 0.5vmin solid var(--color-dark-gray);
  border-radius: 1.5vmin;
  padding: 0px
}

.navButton {
  height: 10vmin;
  width: 20vmin;
  color: rgb(29, 161, 242);
  color: var(--color-primary);
  font-size: 3.5vmin;
  background-color: rgb(245, 248, 250);
  background-color: var(--color-extra-extra-light-gray);
  border: 0.5vmin solid rgb(101, 119, 134);
  border: 0.5vmin solid var(--color-dark-gray);
  border-radius: 1.5vmin;
  margin: 0.5vmin;
  padding-top: 1vmin;
}

.navButton:hover {
  color: rgb(255, 255, 255);
  color: var(--color-white);
  background-color: rgb(29, 161, 242);
  background-color: var(--color-primary);
  font-size: 3.75vmin;
  font-style: italic;
}

.input {
  width: 100%;
  font-size: 3vmin;
  height: 8vmin;
  background-color: rgb(225, 232, 237);
  background-color: var(--color-extra-light-gray);
  vertical-align: center;
  padding: 0px;
}

.dropdown-item {
  font-size: 3vmin;
  height: 4vmin;
}

.form-item {
  font-size: 2.5vmin;
  margin: 0.5vmin;
  padding: 1vmin;
}

.form-item-text {
  font-size: 3vmin;
  padding: 1vmin;
  margin: 0.5vmin;
}

.button {
  margin: 0.5vmin;
  color: rgb(29, 161, 242);
  color: var(--color-primary);
  background-color: rgb(245, 248, 250);
  background-color: var(--color-extra-extra-light-gray);
  border: 0.5vmin solid rgb(101, 119, 134);
  border: 0.5vmin solid var(--color-dark-gray);
  border-radius: 1vmin;
  padding-top: 1vh;
  width: calc(20% - 1vmin);
  font-family: 'Helvetica Neue 75 Bold', Helvetica;
  vertical-align: center;
}

.button:hover {
  color: rgb(255, 255, 255);
  color: var(--color-white);
  background-color: rgb(29, 161, 242);
  background-color: var(--color-primary);
}

#search-row {
  padding: 0.5vmin;
}

#search-box {
  background-color: rgb(170, 184, 194);
  background-color: var(--color-light-gray);
}

#search-button {
}

#search-field {
  width: calc(100% - 1vmin);
}

#search-type, #content-type {
  margin: 0px;
}

.select-search {
  padding-bottom: 2vmin;
}

.custom-select {

  overflow: hidden;
  
}

.custom-select select {
  padding-top: 1vmin;
}

select {
  margin: 0.5vmin;
  padding-left: 2vw;
  padding-top: 2vmin;
  border: 0.5vmin solid rgb(101, 119, 134);
  border: 0.5vmin solid var(--color-dark-gray);
  border-radius: 1vmin;
  width: calc(40% - 1vmin);
  font-size: 2.5vmin;
  color: rgb(255, 255, 255);
  color: var(--color-white);
  overflow: hidden;
  font-family: 'Helvetica Neue 75 Bold', Helvetica;
  box-shadow: none;
  background-color: rgb(29, 161, 242);
  background-color: var(--color-primary);
  height: 6vmin;
}

.custom-select select:focus {
  outline: none;
}

.active {
  background-color: rgb(29, 161, 242);
  background-color: var(--color-primary);
  color: rgb(245, 248, 250);
  color: var(--color-extra-extra-light-gray);
}

.tweet {
  padding: 1.5vmin;
}

.embeddedMedia{
  width: 100%;
  border-radius: 1vmin;
  height: auto;
}

.profile-picture-small {
  vertical-align: middle;
  width: 6vmin;
  height: 6vmin;
  border-radius: 50%;
} 

.header-profile-picture {
  float: left;
  width: 6vmin;
  margin: 1vmin;
}

.header-name {
  margin-top: 1.5vmin;
  float: left;
  width: calc(100% - 10vmin);
}

.user-name {
  margin-bottom: 0.75vmin;
  color: rgb(101, 119, 134);
  color: var(--color-dark-gray);
  font-family: 'Helvetica Neue 55 Roman', Helvetica;
  font-size: 2vmin;
}

.tweetMedia {
  padding: 0px;
  width: calc(100% - 2vmin);
  margin: 1vmin;
}

.tweetBorder {
  padding: 1vmin;
  border: 0.5vmin solid rgb(101, 119, 134);
  border: 0.5vmin solid var(--color-dark-gray);
  border-radius: 0.5vmin 2.5vmin 2.5vmin;
  background-color: rgb(255, 255, 255);
  background-color: var(--color-white);
}

.tweetHeader {
  display: table;
  width: 100%;
  padding: 0px;
  padding-left: 2vmin;
  border: 0.3vmin solid rgb(101, 119, 134);
  border: 0.3vmin solid var(--color-dark-gray);
  background-color: rgb(225, 232, 237);
  background-color: var(--color-extra-light-gray);
  border-radius: 0.5vmin 2.5vmin 1vmin 1vmin;
}

.tweetText {
  color: rgb(20, 23, 26);
  color: var(--color-black);
  font-size: 2.5vmin;
}

.tweetBody {
  padding: 0.5vmin;
}

.tweetFooter {
  display: table;
  width: 100%;
  padding: 0.5vmin;
  font-size: 2vmin;
  color: rgb(101, 119, 134);
  color: var(--color-dark-gray);
}

.hash-tags {
  padding-left: 4vmin;
  float: left;
}

.posted-on {
  padding-right: 2vmin;
  float: right;
}

.textBox {
  padding: 0px 1.5vmin;
}

.textBox.large {
  font-size: 6vh;
}

.textBox.medium {
  font-size: 3vh;
}

.primary {
  color: rgb(29, 161, 242);
  color: var(--color-primary);
}

.black {
  color: rgb(20, 23, 26);
  color: var(--color-black);
}

.dark-gray {
  color: rgb(101, 119, 134);
  color: var(--color-dark-gray);
}

.light-gray {
  color: rgb(170, 184, 194);
  color: var(--color-light-gray);
}

.extra-light-gray {
  color: rgb(225, 232, 237);
  color: var(--color-extra-light-gray);
}

.extra-extra-light-gray {
  color: rgb(245, 248, 250);
  color: var(--color-extra-extra-light-gray);
}

.white {
  color: rgb(255, 255, 255);
  color: var(--color-white);
}

#toTop {
  margin: 0px;
  position: fixed;
  bottom: 1vmin;
  float: right;
  right: 0%;
  font-size: 5vmin;
  color: rgb(29, 161, 242);
  color: var(--color-primary);
  border: 0.5vmin solid rgb(170, 184, 194);
  border: 0.5vmin solid var(--color-light-gray);
  background-color: rgb(225, 232, 237);
  background-color: var(--color-extra-light-gray);
  border-radius: 1vmin;
}

#toTop:hover {
  background-color: rgb(255, 255, 255);
  background-color: var(--color-white);
}

#splash {
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(/static/media/splash.3ba660b5.jpeg);
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(/static/media/splash.3ba660b5.jpeg);
  background-origin: border-box;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center top;
  min-height: 75vh;
}

#page {
  background-color: rgb(245, 248, 250);
  background-color: var(--color-extra-extra-light-gray);
}

.no-padding {
  padding: 0px;
}

.no-margin {
  margin: 0px;
}

.bold {
  font-family: 'Helvetica Neue 75 Bold', Helvetica;
}

.regular {
  font-family: 'Helvetica Neue 55 Roman', Helvetica;
}

@media only screen and (min-width: 768px) {
  .col-1 {width: 12.5%;}
  .col-2 {width: 25%;}
  .col-3 {width: 37.5%;}
  .col-4 {width: 50%;}
  .col-5 {width: 62.5%;}
  .col-6 {width: 75%;}
  .col-7 {width: 87.5%;}
  .col-8 {width: 100%;}
  .navBar {width: 22vmin;}
}
